import React, { useState } from 'react';
import { Card, CardContent, TextField, Button, Typography, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom'; // useNavigate 

export default function Signup() {
  const [name, setName] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(''); //     
  const navigate = useNavigate(); // useNavigate  


  //   
  const handleSignupClick = async () => {
    //   (name, username, password   )
    if (!name || !username || !password) {
      setError('  .'); //   
      return;
    }

    try {
      const response = await fetch('http://10.0.1.38:3001/signup', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: name,
          logid: username,
          password: password,
        }),
      });

      if (response.ok) {
        alert(' ');
        //     
        setName('');
        setUsername('');
        setPassword('');
        setError('');
        navigate('/'); //      
      } else {
        setError(' '); //     
      }
    } catch (error) {
      console.error('   :', error);
      setError(' ');
    }
  };

  return (
    <Box
      sx={{
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#f5f5f5',
      }}
    >
      <Card sx={{ width: 350, height: 500, padding: 5, display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
        <CardContent sx={{ flexGrow: 1 }}>
          <Typography variant="h3" component="div" gutterBottom align="center" sx={{ fontFamily: 'cursive', fontWeight: 'bold' }}>
            
          </Typography>
          <TextField
            fullWidth
            label=""
            variant="outlined"
            margin="normal"
            value={name}
            onChange={(e) => setName(e.target.value)}
            sx={{
              backgroundColor: '#eee',
              borderRadius: '5px',
            }}
          />
          <TextField
            fullWidth
            label=""
            variant="outlined"
            margin="normal"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            sx={{
              backgroundColor: '#eee',
              borderRadius: '5px',
            }}
          />
          <TextField
            fullWidth
            label=""
            type="password"
            variant="outlined"
            margin="normal"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            sx={{
              backgroundColor: '#eee',
              borderRadius: '5px',
            }}
          />
          {/*    */}
          {error && (
            <Typography variant="body2" color="error" align="center" sx={{ mt: 2 }}>
              {error}
            </Typography>
          )}
          <Box sx={{ mt: 15, display: 'flex', justifyContent: 'space-between' }}>
            <Button
              variant="text"
              color="secondary"
              onClick={() => navigate('/')} //   
            >
              
            </Button>
            <Button
              variant="contained"
              color="primary"
              onClick={handleSignupClick} //     
            >
              
            </Button>
          </Box>
        </CardContent>
      </Card>
    </Box>
  );
}
