import React, { useState } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button } from '@mui/material';

export default function AddProfile({ open, handleClose }) {
  const [selectedFile, setSelectedFile] = useState(null);
  const [imageUrl, setImageUrl] = useState(''); //   URL  
  const userId = Number(localStorage.getItem('userId')); //    ID 

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setSelectedFile(file);

    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImageUrl(reader.result); // Base64 URL   
      };
      reader.readAsDataURL(file); //  Base64  
    }
  };

  const handleUpload = () => {
    if (!userId || !selectedFile) {
      alert('  .');
      return;
    }

    const formData = new FormData();
    formData.append('file', selectedFile);
    formData.append('propilid', userId); //  ID  ID 

    fetch('http://10.0.1.38:3001/addprofile', {
      method: 'POST',
      body: formData,
    })
      .then((response) => {
        console.log('   :', response.status); //    
        if (!response.ok) {
          return response.text().then((text) => {
            console.error(' :', text); //     
            throw new Error('   ');
          });
        }
        return response.json();
      })
      .then((data) => {
        console.log('  :', data);
        console.log('  ID:', data.profileId); //  propilid   
        handleClose(); //    
      })
      .catch((error) => {
        console.error('     :', error);
        alert('   .  .');
      });
  };

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle>  </DialogTitle>
      <DialogContent>
        <input type="file" accept="image/*" onChange={handleFileChange} />
        {imageUrl && (
          <img
            src={imageUrl}
            alt=""
            style={{ width: '100%', marginTop: '10px' }}
          />
        )} {/*    */}
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary"></Button>
        <Button onClick={handleUpload} color="primary"></Button>
      </DialogActions>
    </Dialog>
  );
}
