import React, { useState } from 'react';
import { Button, Dialog, DialogTitle, DialogContent, DialogActions, TextField } from '@mui/material';

export default function AddFeed({ open, handleClose, fetchFeeds }) {
  const [selectedFile, setSelectedFile] = useState(null);
  const [feedword, setFeedword] = useState('');
  const userId = localStorage.getItem('userId'); // localStorage userId 

  const handleFileChange = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  const handleUpload = () => {
    if (!userId) {
      alert('  .  .');
      return;
    }

    if (selectedFile && feedword) {
      const formData = new FormData();
      formData.append('file', selectedFile); //  
      formData.append('feedword', feedword); //  
      formData.append('feedid', userId); //   ID (feedid)

      fetch('http://10.0.1.38:3001/addfeed', {
        method: 'POST',
        body: formData,
      })
        .then((response) => response.json())
        .then((data) => {
          console.log('  :', data);

          //   
          fetchFeeds(); //    fetchFeeds  
          setFeedword(''); //

          //    
          handleClose();
        })
        .catch((error) => {
          console.error('  :', error);
        });
    }
  };

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle> </DialogTitle>
      <DialogContent>
        <TextField
          fullWidth
          label=" "
          variant="outlined"
          margin="normal"
          value={feedword}
          onChange={(e) => setFeedword(e.target.value)}
        />
        <input type="file" accept="image/*" onChange={handleFileChange} />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary"></Button>
        <Button onClick={handleUpload} color="primary" disabled={!selectedFile || !feedword}></Button>
      </DialogActions>
    </Dialog>
  );
}
