import React, {useState, useEffect} from 'react';
import {Box, Card, Avatar, Typography, Button} from '@mui/material';
import PostCard from './PostCard'; // PostCard  
import StoryList from "./StoryList";//storylist  

// SuggestedUser 
function SuggestedUser({userName, description, userImage}) {
    return (
        <Box display="flex" justifyContent="space-between" alignItems="center" p={2} borderBottom="1px solid #e0e0e0">
            <Box display="flex" alignItems="center">
                <Avatar src={userImage} alt={userName}>
                    {!userImage && userName[0]?.toUpperCase()} {/*       */}
                </Avatar>
                <Box ml={2}>
                    <Typography fontWeight="bold">{userName}</Typography>
                    <Typography variant="body2" color="textSecondary">{description}</Typography>
                </Box>
            </Box>
            <Button variant="text" color="primary" onClick={() => alert(`: ${userName}`)}>
                
            </Button>
        </Box>
    );
}


// Main Contents 
export default function Contents({refresh}) {
    const [feeds, setFeeds] = useState([]);
    const [loggedInUserName, setLoggedInUserName] = useState('');
    const [profileImageSrc, setProfileImageSrc] = useState(null);
    const userId = Number(localStorage.getItem('userId')); //    ID 


    //    
    const fetchFeeds = () => {
        fetch('http://10.0.1.38:3001/getfeed') //    API 
            .then((response) => response.json())
            .then((data) => {
                const convertedFeeds = data.map((feed) => ({
                    ...feed,
                    fileBlob: new Blob([new Uint8Array(feed.feedview.data)], {type: 'image/jpeg'}), // BLOB   
                }));
                setFeeds(convertedFeeds); //    
            })
            .catch((error) => {
                console.error('     :', error);
            });
    };

    //        
  const handleDeleteFeed = (feedId) => {
    setFeeds((prevFeeds) => prevFeeds.filter((feed) => feed.id !== feedId));
  };

    //      
    const fetchProfileImage = () => {
        fetch(`http://10.0.1.38:3001/getprofile?propilid=${userId}`)
            .then((response) => response.blob())
            .then((blob) => {
                const reader = new FileReader();
                reader.readAsDataURL(blob);
                reader.onloadend = () => {
                    setProfileImageSrc(reader.result); //    
                };
            })
            .catch((error) => {
                console.error('     :', error);
            });
    };

    //       
    useEffect(() => {
        fetchFeeds();
        const userName = localStorage.getItem('name'); //     
        if (userName) {
            setLoggedInUserName(userName); //     
        }
    }, []);

    // refresh      
    useEffect(() => {
        fetchFeeds();
    }, [refresh]);

    useEffect(() => {
        if (userId) {
            fetchProfileImage(); //     
        }
    }, [userId]);

    return (
        <Box p={2} maxWidth={1200} margin="0 auto">
            <Box display="flex" flexDirection={{xs: 'column', md: 'row'}} gap={2}>
                {/*  :  */}
                <Box flex={2}>
                    <Box flex={2}>
                        <Box mb={2}>
                            <StoryList/> {/*    */}
                        </Box>
                        {/*     */}
                        {feeds.map((feed) => (
                            <PostCard
                                id={feed.id}
                                feedid={feed.feedid}
                                userName={feed.userName} //    
                                fileBlob={feed.fileBlob} // Blob   
                                likesCount={feed.feedgood} //  
                                feedword={feed.feedword} //  
                                onDelete={handleDeleteFeed} //     
                            />
                        ))}
                    </Box>
                </Box>

                {/*  :    */}
                <Box flex={1}>
                    <Card style={{padding: '16px'}}>
                        <Box display="flex" alignItems="center">
                            <Avatar sx={{width: 56, height: 56}} src={profileImageSrc || ''}>
                                {loggedInUserName ? loggedInUserName[0].toUpperCase() : 'U'}
                            </Avatar> {/*         */}
                            <Box ml={2}>
                                <Typography fontWeight="bold">{loggedInUserName}</Typography>
                            </Box>
                        </Box>
                        <Box my={2}>
                            <Typography variant="subtitle1" color="textSecondary">
                                  
                            </Typography>
                            <Button variant="text" color="primary"> </Button>
                        </Box>
                        <SuggestedUser userName="samsung" description="hyundai  4 ."/>
                    </Card>
                </Box>
            </Box>
        </Box>
    );
}