import React, { useState } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button } from '@mui/material';

export default function AddStory({ open, handleClose, fetchStories }) {
  const [selectedImage, setSelectedImage] = useState(null);
  const userId = localStorage.getItem('userId'); // localStorage userId 

  const handleImageChange = (event) => {
    setSelectedImage(event.target.files[0]);
  };

  const handleAddStory = () => {
    if (!selectedImage) {
      alert('   .');
      return;
    }

    const formData = new FormData();
    formData.append('story', selectedImage);
    formData.append('Storyid', userId); //   ID

    fetch('http://10.0.1.38:3001/addstory', {
      method: 'POST',
      body: formData,
    })
      .then((response) => response.json())
      .then((data) => {
        console.log('  :', data);
        fetchStories(); //    -  !
        setSelectedImage(null); //    
        handleClose(); //  
      })
      .catch((error) => {
        console.error('    :', error);
      });
  };

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle> </DialogTitle>
      <DialogContent>
        <input
          type="file"
          accept="image/*"
          onChange={handleImageChange} //    
        />
        {selectedImage && (
          <img
            src={URL.createObjectURL(selectedImage)}
            alt=" "
            style={{ width: '100%', marginTop: '10px' }}
          />
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          
        </Button>
        <Button onClick={handleAddStory} color="primary">
          
        </Button>
      </DialogActions>
    </Dialog>
  );
}
