import React, { useState, useEffect } from 'react';
import { Avatar, Box, Typography } from '@mui/material';
import StoryDialog from './StoryDialog'; // StoryDialog  
import { Swiper, SwiperSlide } from 'swiper/react'; // Swiper  
import 'swiper/css'; // Swiper  CSS

function StoryList() {
    const [users, setUsers] = useState([]); //   
    const [selectedStoryId, setSelectedStoryId] = useState(null); //   ID
    const [open, setOpen] = useState(false); //  / 
    const [profileImages, setProfileImages] = useState({}); //     

    //      
    const fetchUsersWithStories = () => {
        fetch('http://10.0.1.38:3001/getusers') //     API 
            .then((response) => response.json())
            .then((data) => {
                setUsers(data); //      
                data.forEach((user) => fetchProfileImage(user.id)); //     
            })
            .catch((error) => {
                console.error('     :', error);
            });
    };

    //    
    const fetchProfileImage = (userId) => {
        fetch(`http://10.0.1.38:3001/getprofile?propilid=${userId}`)
            .then((response) => response.blob())
            .then((blob) => {
                const imageUrl = URL.createObjectURL(blob);
                setProfileImages((prev) => ({
                    ...prev,
                    [userId]: imageUrl, //  ID   
                }));
            })
            .catch((error) => {
                console.error(`      (ID: ${userId}):`, error);
            });
    };

    //      
    const handleProfileClick = (userId) => {
        setSelectedStoryId(userId); //   ID 
        setOpen(true); //  
    };

    //   
    const handleClose = () => {
        setOpen(false); //  
        setSelectedStoryId(null); //  ID 
    };

    //         
    useEffect(() => {
        fetchUsersWithStories();
    }, []);

    return (
        <Box
            display="flex"
            justifyContent="flex-start" //   
            alignItems="center" //     
            p={2}
            overflow="auto"
            sx={{
                backgroundColor: 'white',
                borderRadius: '0px',
                border: '1px solid #e0e0e0',
                boxShadow: '0px 2px 6px rgba(0, 0, 0, 0.05)',
            }}
        >
            {/* Swiper    */}
            <Swiper spaceBetween={20} slidesPerView="auto"> {/*     */}
                {users.map((user) => (
                    <SwiperSlide key={user.id} style={{ width: 'auto' }}>
                        <Box mx={1} textAlign="center" display="flex" flexDirection="column" alignItems="center">
                            <Avatar
                                sx={{
                                    width: 50, //    
                                    height: 50,
                                    bgcolor: '#b39ddb',
                                    cursor: 'pointer',
                                }}
                                onClick={() => handleProfileClick(user.id)} //      
                                src={profileImages[user.id] || ''} //   URL 
                            >
                                {!profileImages[user.id] && user.name[0].toUpperCase()} {/*         */}
                            </Avatar>
                            <Typography variant="caption" sx={{ textAlign: 'center', mt: 1 }}>
                                {user.name}
                            </Typography> {/*    */}
                        </Box>
                    </SwiperSlide>
                ))}
            </Swiper>

            {/*    */}
            <StoryDialog open={open} handleClose={handleClose} storyId={selectedStoryId} />
        </Box>
    );
}

export default StoryList;
