import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Card, CardContent, TextField, Button, Typography, Box } from '@mui/material';

export default function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate(); //    useNavigate 

const handleLoginClick = async () => {
  try {
    const response = await fetch('http://10.0.1.38:3001/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        username,
        password,
      }),
    });

    if (response.ok) {
      const result = await response.json();
      localStorage.setItem('userId', result.id); //    ID 
      localStorage.setItem('name', result.name); //   
      console.log(' userId:', localStorage.getItem('userId')); // localStorage  userId 
      console.log(' name:', localStorage.getItem('name')); // localStorage  name 

      onLogin();
      navigate('/appbar'); //    Appbar 
    } else {
      const errorMessage = await response.text();
      setError(errorMessage); //   
    }
  } catch (error) {
    console.error('   :', error);
    setError(' ');
  }
};

  const handleSignupClick = () => {
    navigate('/signup'); //   
  };

  return (
    <Box
      sx={{
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#f5f5f5',
      }}
    >
      <Card sx={{ width: 350, height: 500, padding: 5, display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
        <CardContent>
          <Typography variant="h3" component="div" gutterBottom align="center" sx={{ fontFamily: 'cursive', fontWeight: 'bold' }}>
            
          </Typography>
          <TextField
            fullWidth
            label=""
            variant="outlined"
            margin="normal"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
          <TextField
            fullWidth
            label=""
            type="password"
            variant="outlined"
            margin="normal"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          {error && (
            <Typography variant="body2" color="error" align="center" sx={{ mt: 2 }}>
              {error}
            </Typography>
          )}
          <Box sx={{ mt: 2 }}>
            <Button
              variant="contained"
              color="primary"
              fullWidth
              onClick={handleLoginClick} //     
            >
              
            </Button>
            <Button
              variant="text"
              color="secondary"
              fullWidth
              onClick={handleSignupClick} //     
              sx={{ mt: 1 }}
            >
              
            </Button>
          </Box>
        </CardContent>
        <Box sx={{ textAlign: 'center', paddingBottom: 2 }}>
          <Button variant="text" color="primary">
             ?
          </Button>
        </Box>
      </Card>
    </Box>
  );
}
