import React, { useState, useEffect } from 'react';
import {
    Avatar,
    Box,
    Card,
    CardHeader,
    CardMedia,
    CardContent,
    IconButton,
    Typography,
    TextField,
    Button
} from '@mui/material';
import { Favorite, ChatBubble, Send, Bookmark, Delete } from '@mui/icons-material';

export default function PostCard({ id, feedid, userName, fileBlob, likesCount, feedword, onDelete }) {
    const [profileImageSrc, setProfileImageSrc] = useState(null); //     
    const [currentLikes, setCurrentLikes] = useState(likesCount); //    
    const [liked, setLiked] = useState(false); //   
    const [imageSrc, setImageSrc] = useState(''); //   
    const [commentsOpen, setCommentsOpen] = useState(false); //  / 
    const [comments, setComments] = useState([]); //   
    const [newComment, setNewComment] = useState(''); //    
    const loggedInUserId = Number(localStorage.getItem('userId'));

    //       (feedid )
    const fetchProfileImage = () => {
        fetch(`http://10.0.1.38:3001/getprofile?propilid=${feedid}`) // feedid      
            .then((response) => response.blob())
            .then((blob) => {
                const reader = new FileReader();
                reader.readAsDataURL(blob);
                reader.onloadend = () => {
                    setProfileImageSrc(reader.result); //    
                };
            })
            .catch((error) => {
                console.error(`      (${userName}):`, error);
            });
    };

    //      
    const checkIfLiked = () => {
        fetch('http://10.0.1.38:3001/checklike', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ feedId: id, userId: loggedInUserId }),
        })
            .then((response) => response.json())
            .then((data) => {
                setLiked(data.liked); //   liked  
            })
            .catch((error) => {
                console.error('     :', error);
            });
    };

    //   
    const fetchComments = () => {
        fetch(`http://10.0.1.38:3001/getcomments/${id}`)
            .then((response) => response.json())
            .then((data) => {
                setComments(data); //   
            })
            .catch((error) => {
                console.error('    :', error);
            });
    };

    //  Blob  
    useEffect(() => {
        if (fileBlob) {
            const reader = new FileReader();
            reader.readAsDataURL(fileBlob);
            reader.onloadend = () => {
                setImageSrc(reader.result); //    
            };
        }
    }, [fileBlob]);

    //        
    useEffect(() => {
        fetchProfileImage();
        checkIfLiked();
        fetchComments();
    }, [userName, loggedInUserId]);

    const handleLikeClick = () => {
        const newLikedState = !liked;

        setCurrentLikes((prevLikes) => (newLikedState ? prevLikes + 1 : prevLikes - 1));
        setLiked(newLikedState);

        fetch('http://10.0.1.38:3001/likefeed', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ feedId: id, userId: loggedInUserId, liked: newLikedState }),
        }).catch((error) => console.error('    :', error));
    };

    const handleDeleteClick = () => {
        fetch(`http://10.0.1.38:3001/deletefeed/${id}`, { method: 'DELETE' })
            .then((response) => response.json())
            .then((data) => {
                if (data.success && onDelete) onDelete(id);
            })
            .catch((error) => console.error('    :', error));
    };

    const handleCommentSubmit = () => {
        if (!newComment.trim()) return;

        const commentData = {
            udid: id,
            logid: loggedInUserId,
            udcont: newComment,
        };

        fetch('http://10.0.1.38:3001/addcomment', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(commentData),
        })
            .then((response) => response.json())
            .then((data) => {
                if (data.success) {
                    fetchComments(); //  
                    setNewComment(''); //  
                }
            })
            .catch((error) => {
                console.error('    :', error);
            });
    };

    const handleCommentToggle = () => {
        setCommentsOpen(!commentsOpen);
    };

    const heartIconStyle = {
        color: liked ? 'red' : 'white',
        stroke: liked ? 'none' : 'black',
        strokeWidth: 1.5,
    };

    const iconStyle = {
        color: 'white',
        stroke: 'black',
        strokeWidth: 1.5,
    };

    return (
        <Card style={{ marginBottom: '20px' }}>
            <CardHeader
                avatar={
                    <Avatar aria-label="profile" src={profileImageSrc || ''}>
                        {!profileImageSrc && userName[0].toUpperCase()}
                    </Avatar>
                }
                title={userName}
                action={
                    loggedInUserId === feedid && (
                        <IconButton aria-label="delete" onClick={handleDeleteClick}>
                            <Delete />
                        </IconButton>
                    )
                }
            />
            {imageSrc && (
                <CardMedia
                    component="img"
                    height="800"
                    image={imageSrc}
                    alt="Post image"
                />
            )}
            <CardContent>
                <Box display="flex" justifyContent="space-between">
                    <Box display="flex">
                        <IconButton aria-label="add to favorites" onClick={handleLikeClick}>
                            <Favorite sx={heartIconStyle} />
                        </IconButton>
                        <IconButton aria-label="comment" onClick={handleCommentToggle}>
                            <ChatBubble sx={iconStyle} />
                        </IconButton>
                        <IconButton aria-label="send">
                            <Send sx={iconStyle} />
                        </IconButton>
                    </Box>
                    <IconButton aria-label="bookmark">
                        <Bookmark sx={iconStyle} />
                    </IconButton>
                </Box>
                <Typography variant="body2" component="div" fontWeight="bold">
                     {currentLikes}
                </Typography>
                <Typography variant="body2" component="div" color="textSecondary">
                    {feedword}
                </Typography>

                {/*   */}
                {commentsOpen && (
                    <Box mt={2}>
                        <Typography variant="body2" fontWeight="bold">
                            
                        </Typography>
                        {comments.map((comment) => (
                            <Box key={comment.id} mt={1}>
                                <Typography variant="body2" fontWeight="bold">
                                    {comment.name}
                                </Typography>
                                <Typography variant="body2" color="textSecondary">
                                    {comment.udcont}
                                </Typography>
                            </Box>
                        ))}
                        <Box mt={2}>
                            <TextField
                                fullWidth
                                placeholder=" "
                                value={newComment}
                                onChange={(e) => setNewComment(e.target.value)}
                            />
                            <Button onClick={handleCommentSubmit} variant="contained" sx={{ mt: 1 }}>
                                 
                            </Button>
                        </Box>
                    </Box>
                )}
            </CardContent>
        </Card>
    );
}
