import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import AppBar from './title/Appbar'; // AppBar 
import Login from './title/login'; // Login 
import Signup from './title/new'; // Signup 

export default function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false); //   

  //     
  const handleLogin = () => {
    setIsLoggedIn(true);
    // localStorage.setItem('isLoggedIn', 'true'); // localStorage    
  };

  //  
  const handleLogout = () => {
    setIsLoggedIn(false);
    // localStorage.removeItem('isLoggedIn'); // localStorage   
  };

  //        
  useEffect(() => {
    //      false 
    setIsLoggedIn(false);
  }, []);

  return (
    <Router>
      <Routes>
        <Route
          path="/"
          element={
            isLoggedIn ? <Navigate to="/appbar" /> : <Login onLogin={handleLogin} />
          }
        />
        <Route path="/signup" element={<Signup />} /> {/*   */}
        <Route
          path="/appbar"
          element={
            isLoggedIn ? <AppBar onLogout={handleLogout} /> : <Navigate to="/" />
          }
        /> {/*    AppBar  */}
      </Routes>
    </Router>
  );
}
