import React, { useState, useEffect } from 'react';
import { Dialog, DialogTitle, DialogContent } from '@mui/material';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css'; // Swiper  CSS
import 'swiper/css/navigation'; //   CSS ( )
import 'swiper/css/pagination'; //   CSS ( )

export default function StoryDialog({ open, handleClose, storyId }) {
  const [stories, setStories] = useState([]); //   

  //    
  const fetchStory = () => {
    fetch(`http://10.0.1.38:3001/getstory/${storyId}`)
      .then((response) => response.json())
      .then((data) => {
        setStories(data); //     
      })
      .catch((error) => {
        console.error('     :', error);
      });
  };

  useEffect(() => {
    if (storyId && open) {
      fetchStory(); //  ID    
    }
  }, [storyId, open]);

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle> </DialogTitle>
      <DialogContent>
        {stories.length > 0 ? (
          <Swiper spaceBetween={10} slidesPerView={1}>
            {stories.map((storyImage, index) => (
              <SwiperSlide key={index}>
                <img src={storyImage} alt={` ${index + 1}`} style={{ width: '100%' }} />
              </SwiperSlide>
            ))}
          </Swiper>
        ) : (
          <p>  ...</p>
        )}
      </DialogContent>
    </Dialog>
  );
}
