import React, { useState, useEffect } from 'react';
import {
  Box,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Avatar,
  Badge,
  Typography,
  IconButton,
  TextField,
  Button,
  Paper,
} from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

const Message = () => {
  const [selectedUser, setSelectedUser] = useState(null); //   
  const [messages, setMessages] = useState([]); //  
  const [inputValue, setInputValue] = useState(''); //  
  const [users, setUsers] = useState([]); //   
  const loggedInUserId = Number(localStorage.getItem('userId')); //     ID 

  //    
  const markMessagesAsRead = (messageIds, partnerId) => {
    fetch('http://10.0.1.38:3001/markmessagesread', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        userId: loggedInUserId, //   ID
        partnerId: partnerId, //   ID
        messageIds //    ID 
      }),
    })
      .then(() => {
        //      unreadMessages 
        setUsers(prevUsers =>
          prevUsers.map(user =>
            user.id === partnerId
              ? { ...user, unreadMessages: 0 }
              : user
          )
        );
      })
      .catch(error => console.error('    :', error));
  };

  //         +  
  const handleUserSelect = (user) => {
    setSelectedUser(user);
    fetch(`http://10.0.1.38:3001/messages/${loggedInUserId}/${user.id}`)
      .then((response) => response.json())
      .then((data) => {
        setMessages(data); //    

        //      
        const unreadMessageIds = data.filter((msg) => !msg.is_read).map((msg) => msg.id);
        if (unreadMessageIds.length > 0) {
          markMessagesAsRead(unreadMessageIds, user.id);
        }
      })
      .catch((error) => console.error('   :', error));
  };

  //  
  const handleMessageSend = () => {
    if (inputValue.trim()) {
      const newMessage = {
        sender_id: loggedInUserId, //    ID 
        receiver_id: selectedUser.id,
        text: inputValue,
      };

      //   API 
      fetch('http://10.0.1.38:3001/messages', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newMessage),
      })
        .then((response) => response.json())
        .then((data) => {
          //    UI 
          setMessages([...messages, { ...newMessage, id: data.messageId }]);
          setInputValue('');
        })
        .catch((error) => console.error('   :', error));
    }
  };

  //    
  useEffect(() => {
    fetch(`http://10.0.1.38:3001/messageusers?userId=${loggedInUserId}`) //    API 
      .then((response) => response.json())
      .then((data) => {
        //     
        const filteredUsers = data.filter((user) => user.id !== loggedInUserId);
        setUsers(filteredUsers); //    
      })
      .catch((error) => console.error('    :', error));
  }, [loggedInUserId]);

  return (
    <Box p={2} maxWidth={1200} margin="0 auto">
      <Box display="flex" height="100vh">
        {/* :   */}
        <Box width="30%" bgcolor="#F9F9F9" p={2}>
          <Typography variant="h6" gutterBottom>
            
          </Typography>
          <List>
            {users.map((user) => (
              <ListItem button key={user.id} onClick={() => handleUserSelect(user)}>
                <ListItemAvatar>
                  <Avatar />
                </ListItemAvatar>
                <ListItemText primary={user.name} secondary="" />
                {user.unreadMessages > 0 && ( //       
                  <Badge badgeContent={user.unreadMessages} color="error" />
                )}
              </ListItem>
            ))}
          </List>
        </Box>

        {/* :   */}
        <Box width="70%" display="flex" flexDirection="column" borderLeft="1px solid #ddd">
          {/* :   */}
          {selectedUser ? (
            <>
              <Box display="flex" alignItems="center" bgcolor="#F1F1F1" p={2}>
                <IconButton onClick={() => {
                  setSelectedUser(null);
                  setMessages([]); //   
                  setInputValue(''); //   
                }}>
                  <ArrowBackIcon />
                </IconButton>
                <Typography variant="h6">{selectedUser.name}</Typography>
              </Box>

              {/*   */}
              <Box flex={1} p={2} bgcolor="#FFF">
                {messages.map((message) => (
                  <Box
                    key={message.id} //  key 
                    display="flex"
                    justifyContent={message.sender_id === loggedInUserId ? 'flex-end' : 'flex-start'} // sender_id  
                    mb={2}
                  >
                    {message.sender_id !== loggedInUserId && <Avatar sx={{ mr: 1 }} />}
                    <Paper
                      elevation={1}
                      sx={{
                        p: 1,
                        maxWidth: '60%',
                        bgcolor: message.sender_id === loggedInUserId ? '#2979ff' : '#e0e0e0',
                        color: message.sender_id === loggedInUserId ? 'white' : 'black',
                      }}
                    >
                      {message.text}
                    </Paper>
                  </Box>
                ))}
              </Box>

              {/*  */}
              <Box p={2} display="flex" bgcolor="#F1F1F1">
                <TextField
                  variant="outlined"
                  placeholder=" "
                  fullWidth
                  value={inputValue}
                  onChange={(e) => setInputValue(e.target.value)}
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') handleMessageSend();
                  }}
                />
                <Button variant="contained" color="primary" onClick={handleMessageSend} sx={{ ml: 1 }}>
                  
                </Button>
              </Box>
            </>
          ) : (
            <Box display="flex" justifyContent="center" alignItems="center" height="100%">
              <Typography variant="h6"> </Typography>
            </Box>
          )}
        </Box>
      </Box>
    </Box>
  );
};

export default Message;
